@import url(https://fonts.googleapis.com/css2?family=Lato:wght@400;700&family=Raleway:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

[class*="col-"] {
  float: left;
}

.card-general {
  background-color: rgba(255, 255, 255, 0.7);
  margin: 10px;
  padding: 20px;
}

.row::after {
  content: "";
  clear: both;
  display: table;
}
html {
  scroll-behavior: smooth;
}

.page-box {
  max-width: 1200px;
  margin: auto;
  padding: 70px 0 100px;
}

section {
  float: left;
  width: 100%;
}

section:not(#home) {
  padding-bottom: 100px;
}

.section-header {
  font-size: 40px;
  font-family: raleway;
  text-transform: uppercase;
  text-align: center;
  font-weight: bold;
  color: #1b242f;
}

.section-header-bar {
  height: 4px;
  background-color: #1b242f;
  width: 100px;
  margin: 20px auto 40px;
}

body {
  background-color: white;
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
  height: 100vh;
  font-family: raleway;
}

.App {
  min-height: 100vh;
}

/* For mobile phones: */
[class*="col-"] {
  width: 100%;
}

/* For tablets (+ desktop): */
@media only screen and (min-width: 900px) {
  .col-1 {
    width: 8.33%;
  }
  .col-2 {
    width: 16.66%;
  }
  .col-3 {
    width: 25%;
  }
  .col-4 {
    width: 33.33%;
  }
  .col-5 {
    width: 41.66%;
  }
  .col-6 {
    width: 50%;
  }
  .col-7 {
    width: 58.33%;
  }
  .col-8 {
    width: 66.66%;
  }
  .col-9 {
    width: 75%;
  }
  .col-10 {
    width: 83.33%;
  }
  .col-11 {
    width: 91.66%;
  }
  .col-12 {
    width: 100%;
  }
  .section-header {
    font-size: 50px;
  }
}

/* For desktop: */
@media only screen and (min-width: 768px) {
}

.title-page {
  background-color: grey;
  height: calc(100vh);
  background: url(/portfolio/static/media/mountains.8f47f098.jpg);
  background-repeat: no-repeat;
  background-size: cover;
}

.title-content {
  position: absolute;
  top: 30%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 70%;
}

.title-welcome {
  color: black;
  font-size: 30px;
  font-family: "raleway";
  text-align: center;
  background-color: rgb(255, 255, 255, 0.5);
  padding: 10px;
}

.title-name {
  color: crimson;
}

.title-content svg {
  margin-top: 20px;
  width: 48px;
  cursor: pointer;
  fill: rgb(255, 255, 255, 0.7);
  transition: fill 0.1s;
}

.title-content svg:hover {
  fill: white;
}

/* For tablets (+ desktop): */
@media only screen and (min-width: 900px) {
  .title-content {
    width: auto;
  }

  .title-welcome {
    font-size: 40px;
  }
}

nav {
  width: 100%;
  background-color: #1b242f;
  border-bottom: 3px solid #04c2c9;
}

/* Height will change for mobile! */
.link-wrap {
  width: 100%;
  height: 50px;
  overflow: hidden;
  transition: height 0.5s;
}

.nav-hamburger {
  width: 100%;
  height: 50px;
}

.nav-hamburger img {
  float: right;
  padding: 13px 20px;
}

.nav-link {
  float: left;
  font-size: 20px;
  line-height: 20px;
  text-transform: uppercase;
  color: white;
  transition: color 0.5s;
  cursor: pointer;
  padding: 15px 0 15px 40px;
  width: calc(100% - 40px);
  background-color: #333;
}

nav .active,
.nav-link:hover {
  color: #e31b6d;
}

@media only screen and (min-width: 768px) {
  .link-wrap {
    width: calc(100% - 30px);
    margin-left: 30px;
  }

  .nav-hamburger {
    display: none;
  }

  .nav-link {
    width: auto;
    background-color: #1b242f;
    padding: 15px 0 15px 80px;
  }
}

#about {
  background-color: white;
}

.introduction {
  margin-bottom: 20px;
}

.about-pfp {
  margin: auto;
  width: 200px;
}

.about-pfp img {
  border-radius: 50%;
  width: 200px;
}

.about-intro {
  max-width: 560px;
  margin: 20px;
  text-align: center;
  font-size: 18px;
}

.about-intro-title {
  font-weight: bold;
  font-size: 24px;
}

.social-link {
  display: block;
  color: black;
  text-decoration: none;
  width: 50%;
  float: left;
}

.social-link svg {
  margin: 16px;
  float: left;
  padding-left: 20px;
}

.social-link-title {
  float: left;
  width: 80px;
  line-height: 20px;
  height: 20px;
  margin-top: 22px;
  font-weight: bold;
}

.skillbar {
  margin: 15px;
  float: left;
  margin-left: 5%;
  max-width: 90%;
}

.skillbar-title {
  font-size: 16px;
  line-height: 30px;
  height: 30px;
  text-align: center;
  background-color: #04c2c9;
  color: white;
  font-weight: bold;
  width: 25%;
  float: left;
}

.skillbar-bar {
  background-color: #eee;
  height: 30px;
  width: 75%;
  float: left;
}

.skillbar-fill {
  background-color: #00a1a7;
  height: 100%;
  float: left;
}

.skillbar-percent {
  width: calc(10% - 2px);
  float: right;
  text-align: right;
  font-size: 13px;
  line-height: 16px;
  margin-top: 6px;
  margin-right: 2px;
}

/* For tablets (+ desktop): */
@media only screen and (min-width: 900px) {
  .social-link svg {
    padding-left: 0px;
  }

  .social-link {
    width: 25%;
  }

  .skillbar-title {
    font-size: 18px;
  }

  .skillbar {
    margin-left: 8%;
  }

  .skillbar-percent {
    font-size: 14px;
  }

  .introduction {
    margin-bottom: 0;
  }
}

#projects {
  background-color: #f5f5f5;
}

.project-list {
  margin: 20px auto;
  max-width: 768px;
}

.project-tile {
  height: 300px;
  background-color: lightskyblue;
  background-size: cover;
  background-position: center;
  width: 100%;
  max-width: 100%;
}

.project-info {
  color: white;
  opacity: 0;
  transition: opacity 0.3s;
  background-color: rgb(0, 0, 0, 0.7);
  height: 100%;
  text-align: center;
  padding: 0 10%;
  z-index: 0;
}

.project-info:hover {
  opacity: 1;
}

.sticky {
  z-index: 5;
}

.project-content {
  height: 75%;
}

.project-title {
  padding-top: 25px;
  font-size: 30px;
}

.project-text {
  padding-top: 16px;
  padding-bottom: 16px;
  margin: auto;
}

.project-tech {
  color: crimson;
  background-color: rgb(255, 255, 255, 0.6);
  display: inline-block;
  padding: 3px;
  font-weight: bold;
  margin: 5px;
  font-size: 14px;
}

.project-link {
  font-size: 18px;
  border: 2px solid white;
  display: block;
  padding: 8px;
  cursor: pointer;
  text-transform: uppercase;
  width: 50%;
  margin: 5px auto;
}

@media only screen and (min-width: 768px) {
  .project-tile {
    width: 50%;
  }

  /* Centre final tile if odd number */
  .project-tile:last-child:nth-child(odd) {
    margin-left: 25%;
  }
}

@media only screen and (min-width: 1200px) {
  .project-tile {
    width: 33.3%;
  }

  .project-tile:last-child:nth-child(odd) {
    margin-left: 0;
  }

  .project-list {
    max-width: 100%;
  }
}

.popup {
  position: fixed;
  background-color: rgb(0, 0, 0, 0.25);
  z-index: 10;
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
}

.popup-content {
  background-color: white;
  position: absolute;
  padding: 40px;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  max-height: calc(100vh - 40px);
  overflow-y: scroll;
  margin: 20px 0;

  width: 90%;
  max-width: 700px;
}

.popup-content::-webkit-scrollbar {
  display: none;
}

.popup-exit-button {
  position: absolute;
  top: 20px;
  right: 40px;
  cursor: pointer;
}

@media only screen and (min-width: 900px) {
  .popup-exit-button {
    right: 20px;
  }
}

.popup-exit-button:hover {
  fill: crimson;
}

.popup-title {
  font-size: 34px;
  text-align: center;
  font-weight: bold;
  text-transform: uppercase;
  margin-bottom: 30px;
}

.popup-text {
  background-color: #e9e9e9;
  padding: 20px;
  margin-top: 10px;
}

.popup-links {
  font-size: 14px;
}

.popup-links a {
  display: block;
  margin: 5px 0 0;
  overflow-x: hidden;
}

.popup-images {
  padding: 10px;
}

.popup-techs {
  padding-top: 10px;
  text-align: center;
}

.popup-tech {
  display: inline-block;
  margin: 5px;
  padding: 3px;
  background-color: white;
  color: crimson;
  font-weight: bold;
  font-size: 14px;
}

#skills {
  background-color: white;
}

.skills-wrap {
  max-width: 1000px;
  margin: auto;
}

.skill-box {
  height: 360px;
  margin-bottom: 20px;
  width: 50%;
  float: left;
}

.skill-logo {
  width: 120px;
  margin: 20px auto;
}

.skill-text {
  width: 80%;
  margin: auto;
  text-align: center;
}

.skill-title h1 {
  font-size: 1.25em;
}

.skill-content {
  font-size: 14px;
}

@media only screen and (min-width: 600px) {
  .skill-title h1 {
    font-size: 1.5em;
  }
}

@media only screen and (min-width: 1200px) {
  .skill-box {
    width: 25%;
  }
}

